@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Nunito:wght@400;600;700&display=swap);




:root {
    --primary-yellow: #FFBC01;
    --text-black: #181818;
    --text-grey: #494949;
    --background-grey: #F7F7F7;
    --icon-grey: #DADADA
}

* {
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

ul {
    margin: 0;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

li {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: inherit;
}

.wrapper {
    margin: 0 auto;
    width: 1224px;
}

.text--yellow {
    color: #FFBC01;
    color: var(--primary-yellow);
}

.button--yellow-border {
    margin-right: 28px;
    background-color: #FFFFFF;
    border: 2px solid #FFBC01;
    border: 2px solid var(--primary-yellow);
    border-radius: 10px;
    font-size: 20px;
    color: #FFBC01;
    color: var(--primary-yellow);
    cursor: pointer;
}

.button--yellow-border:hover {
    background-color: #FFBC01;
    background-color: var(--primary-yellow);
    color: #FFFFFF;
}

.button--yellow-background {
    border: none;
    border-radius: 10px;
    background-color: #FFBC01;
    background-color: var(--primary-yellow);
    color: #181818;
    color: var(--text-black);
    font-size: 20px;
    font-weight: bold;
    line-height: 30px;
    cursor: pointer;
}

.button--yellow-background:disabled {
    background-color: #DADADA;
}

.button--low {
    padding: 12px 32px;
}

.button--high {
    padding: 16px 32px;
}
.header {
    height: 88px;
}

.header--row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-button {
    background-color: inherit;
    cursor: pointer;
}

.header .button--yellow-border {
    width: 225px;
    height: 54px;
}

.header-button--no-border {
    padding-left: 25px;
    border: none;
    font-size: 16px;
}

.header-button--icon {
    background-image: url(/itechart-internship/static/media/profile-icon.afb9a64f.svg);
    background-repeat: no-repeat;
}
.logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo.big {
    width: 137px;
    height: 40px;
}

.logo-image.big {
    width: 21px;
}

.logo.small {
    width: 112px;
    height: 33px;
}

.logo-image.small {
    width: 17px;
}

.logo-title {
    font-family: 'Nunito', sans-serif;
}

.logo-title.big {
    font-size: 29px;
}

.logo-title.small {
    font-size: 24px;
}

.logo-text--yellow {
    color: var(--primary-yellow);
}

.logo-text--black {
    color: var(--text-black);
}

.logo-text--white {
    color: var(--background-grey);
}
.nav-list {
    height: 100%;
    display: flex;
    align-items: center;

    font-family: 'Nunito', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
}

.nav-item:not(:last-of-type) {
    margin-right: 72px;
}

.nav-link.nav-link--black {
    color: var(--text-black);
}

.nav-link.nav-link--white {
    color: var(--background-grey);
}

.nav-link:hover {
    color: var(--primary-yellow);
    text-decoration: underline;
}
.search-input {
    width: 284px;
    height: 40px;
    padding-left: 40px;
    border: none;
    border-radius: 10px;
    background-color: var(--background-grey);
    background-image: url(/itechart-internship/static/media/search-icon.034080bc.svg);
    background-repeat: no-repeat;
    background-position: 16px 12px;
}
.footer {
    height: 97px;
    background-color: var(--text-black);
}
.footer--row {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.mail-link {
    color: var(--primary-yellow);
    line-height: 18px;
}

.project-info {
    height: 100%;
    display: flex;
    align-items: center;
}

.project-info--text {
    margin-right: 16px;
    color: #DADADA;
}
.modal-window--container {
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
}

.modal-overlay {
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(24,24,24,0.5);
    overflow: auto;
}

.modal {
    position: absolute;
    top: 168px;
    margin-bottom: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
}

.modal-window {
    width: 808px;
    max-width: 100%;
    padding: 72px 56px;
    z-index: 1;
    background: rgba(255,255,255,1);
    border-radius: 10px;
    color: var(--text-black);
}

.close-modal-button {
    margin-bottom: 80px;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.modal-close-button--icon {
    color: var(--icon-grey);
    font-size: 35px;
}

.modal-window-title {
    margin-bottom: 40px;
    font-family: "Montserrat", sans-serif;
    font-weight: 800;
    font-size: 40px;
    line-height: 48px;
}

.modal-window-form {
    width: 696px;
}

.modal-window-form .inputs-list {
    margin-bottom: 56px;
}

.inputs-list .button {
    margin-top: 24px;
}

.modal-window-form--label {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

.buttons-list {
    margin-top: 55px;
}

.file-input::-webkit-file-upload-button {
    visibility: hidden;
}

.file-input {
    padding-top: 24px;
    height: 70px;
}

.file-input::before {
    padding: 16px 32px;
    border: none;
    border-radius: 10px;
    background-color: var(--primary-yellow);
    color: var(--text-black);
    font-size: 20px;
    font-weight: bold;
    line-height: 30px;
    cursor: pointer;
}

.recipe-file-input::before {
    content: "Upload Recipe Image";
}

.cookbook-file-input::before {
    content: "Upload Cookbook Image";
}

.recipe-file-input:hover::before {
    color: #FFFFFF;
}
.signup-page {
    padding: 32px 0 32px 32px;
    height: calc(100vh);
    display: flex;
    align-items: center;
    background-color: var(--background-grey);
}

.signup--background {
    width: 1004px;
    height: 960px;
    border-radius: 50px 10px;
    background-image: url(/itechart-internship/static/media/signup-bg.cda552bb.jpg);
}

.form-container {
    width: 600px;
    padding: 72px 56px;
    border-radius: 50px 10px;
    background-color: #FFFFFF;
    z-index: 1;
}

.form--font {
    font-family: 'Nunito', sans-serif;
}

.form-title {
    margin-top: 48px;
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: bold;
    line-height: 48px;
}

.form-subtitle {
    margin-top: 8px;
    margin-bottom: 40px;
    font-size: 16px;
    font-weight: 600;
    line-height: 22px;
}

.form-link--yellow {
    font-weight: 600;
    color: var(--primary-yellow);
    text-decoration: none;
}

.form-input {
    margin-top: 8px;
    height: 54px;
    border: 1px solid #DADADA;
    border-radius: 8px;
}

.form-input--text {
    font-size: 14px;
    line-height: 19px;
    color: var(--text-grey);
}

.form--column {
    display: flex;
    flex-direction: column;
}

.form--row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.form-input--margin {
    margin-top: 32px;
}

.form-error--text {
    margin-top: 5px;
    color: #DC143C;
}

.form--translate-right {
    transform: translate(185px);
}

.form--translate-left {
    transform: translate(-185px);
}

.form-container .button--yellow-background {
    width: 488px;
    height: 54px;
    margin-top: 56px;
}

.form-submit-button:disabled {
    background-color: #494949;
}

.signin-page {
    padding: 32px 0 32px 32px;
    height: calc(100vh);
    display: flex;
    align-items: center;
    background-color: var(--background-grey);
}

.signin--background {
    width: 1004px;
    height: 960px;
    border-radius: 50px 10px;
    background-image: url(/itechart-internship/static/media/sign-in-bg.8da7ab2c.jpg);
}

.profile {
    padding: 104px;
    background-color: var(--background-grey);
}


.profile-info {
    margin-bottom: 55px;
    display: flex;
    align-items: center;
}

.profile-avatar {
    margin-right: 40px;
    height: 160px;
    width: 160px;
    border-radius: 100px;
}

.profile-description {
    color: var(--text-black);
}
.profile-name {
    margin-bottom: 20px;
    font-family: "Montserrat", sans-serif;
    font-weight: 800;
    font-size: 40px;
    line-height: 48px;
}

.profile-bio {
    width: 500px;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    line-height: 22px;
}

.profile-avatar--container {
    display: flex;
    position: relative;
}
.user-photo-input {
    position: absolute;
    top: 0;
    left: 0;
    height: 160px;
    width: 160px;
    background-color: var(--text-black);
    opacity: 50%;
    background-image: url(/itechart-internship/static/media/avatar-upload.efb57fc5.svg);
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    z-index: 1;
    cursor: pointer;
}

.user-photo-input::-webkit-file-upload-button {
    visibility: hidden;
}

.profile-nav--flex {
    margin-bottom: 40px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.profile-nav--flex .button {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    color: var(--text-black);
    border: none;
    cursor: pointer;
}

.profile-nav-list {
    display: flex;
}

.profile-nav-item:not(:last-of-type) {
    margin-right: 32px;
}

.profile-nav-button--no-border {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 26px;
    background-color: inherit;
}

.profile-nav-button--no-border:hover {
    border-bottom: 2px solid var(--primary-yellow);
}

.profile-nav-button--yellow {
    width: 273px;
    height: 62px;
    border-radius: 10px;
    background-color: var(--primary-yellow);
}

.profile-nav-button--yellow:hover {
    color: #FFFFFF;
}

.profile-nav--flex .active-button {
    border-bottom: 2px solid var(--primary-yellow);
}

.recipe-tab {
    width: 100%;
    height: 212px;
    display: flex;
    background: #FFFFFF;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.08);
    border-radius: 50px 10px 10px 10px;
    cursor: pointer;
}

.recipe-tab:not(:last-of-type) {
    margin-bottom: 24px;
}

.recipe-tab--column {
    width: 100%;
    padding: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

.recipe-tab:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.recipe-tab--margin {
    margin-bottom: 16px;
}

.tab-text {
    font-size: 14px;
    line-height: 19px;
    color: var(--text-black);
}

.recipe-tab-photo {
    width: 288px;
    height: 212px;
    border-radius: 50px 10px;
}


.recipe-tab-stats-list {
    display: flex;
    align-items: center;
}

.recipe-tab--space-between {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.recipe-tab-name {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
}
.tab-stats {
    display: flex;
    align-items: center;
}

.tab-text {
    font-size: 14px;
    line-height: 19px;
    color: var(--text-black);
}

.tab-stats:not(:last-of-type) {
    margin-right: 24px;
}

.icon {
    margin-right: 8px;
    color: var(--icon-grey);
}

.tab:hover .icon {
    color: var(--primary-yellow);
}
.cookbook-tabs-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.cookbook-tab {
    width: 288px;
    height: 487px;
    padding: 24px;
    margin-bottom: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    border-radius: 10px;
    background: #FFFFFF;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.08);
    color: var(--text-black);
    cursor: pointer;
}

.cookbook-tab:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.cookbook-tab--margin {
    margin-bottom: 16px;
}

.tab-text {
    font-size: 14px;
    line-height: 19px;
    color: var(--text-black);
}

.cookbook-tab-photo {
    width: 240px;
    height: 240px;
    border-radius: 50px 10px;
}


.cookbook-tab-stats-list {
    display: flex;
    align-items: center;
}


.cookbook-tab-title {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.cookbook-tab-name {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
}



.profile-edit-card {
    height: calc(100vh - 710px);
    width: 100%;
    padding: 72px 56px;
    background-color: #FFFFFF;
    box-shadow: 0 0 16px rgba(0, 0, 0, 0.08);
    border-radius: 10px;
    color: var(--text-black);
}

.profile-edit-title {
    margin-bottom: 32px;
    font-family: "Montserrat", sans-serif;
    font-weight: 800;
    font-size: 40px;
    line-height: 48px;
}

.profile-edit--flex {
    display: flex;
}

.profile-edit-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
}

.profile-edit-text:first-child {
    margin-right: 72px;
}


.ingredients-item {
    width: 250px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.delete-ingredient-button {
    font-size: 24px;
    color: var(--text-grey);
    cursor: pointer;
}
